import React from 'react';
import './SearchBar.css';
import {ClipLoader} from "react-spinners";

const sortByOptions = {
    'Best Match': 'best_match',
    'Highest Rated': 'rating',
    'Most Reviewed': 'review_count'
};

const barLoaderCss = `
    margin-top: 1.5rem;
    width: 40px;
    height: 40px;
    position: absolute;
    `;

class SearchBar extends React.Component {
    constructor (props){
        super(props);
        this.state = {
            term: '',
            location: '',
            sortBy: 'best_match',
            termError: true,
            locationError: true,
            loading: true
        };

        this.handleTermChange = this.handleTermChange.bind(this);
        this.handleLocationChange = this.handleLocationChange.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.keyPressed = this.keyPressed.bind(this);
        this.validateFun = this.validateFun.bind(this);
    }
    //call function when press Enter
    keyPressed = event => {
        if(event.key === 'Enter'){
            this.handleSearch();
        }
    };
    //check if input is empty
    validateFun = () => {
        const {term, location} = this.state;
        if (!term && !location ){
            this.setState({termError: false});
            this.setState({locationError: false});
        }

        if (!term){
            this.setState({termError: false});
        }

        if (!location){
            this.setState({locationError: false});
        }

        if (term && !location ){
            this.setState({termError: true});
            this.setState({locationError: false});
        }

        if (!term && location ){
            this.setState({termError: false});
            this.setState({locationError: true});
        }

    };

    handleSearch(event) {
        const {term, location, sortBy} = this.state;


        if(!term || !location){
            this.validateFun();
            return
        }

        if(term && location){
            this.setState({termError: true});
            this.setState({locationError: true});
        }


        this.props.searchYelp(term, location, sortBy);
        event && event.preventDefault();
    }

    handleTermChange(event) {
        this.setState({term: event.target.value});
    }

    handleLocationChange(event) {
        this.setState({location: event.target.value});
    }

    getSortByClass(sortByOption){
        if (sortByOption === this.state.sortBy){
            return 'active';
        }
        return '';
        };
    handleSortByChange(sortByOption){
        this.setState({sortBy: sortByOption},() => {
            this.handleSearch();
        });
    }


    renderSortByOptions(){
        return Object.keys(sortByOptions).map(sortByOption => {
            let sortByOptionValue = sortByOptions[sortByOption];
            return <li key={sortByOptionValue} onClick={this.handleSortByChange.bind(this, sortByOptionValue)} className={this.getSortByClass(sortByOptionValue)}>{sortByOption} </li>;
        });
    }
    render (){
        return (
            <div className="SearchBar">
                <div className="SearchBar-sort-options">
                    <ul>
                        {this.renderSortByOptions()}
                    </ul>
                </div>
                <div className="SearchBar-fields" >
                    <input className={this.state.termError ? '' : 'input'} placeholder="Search Businesses" onChange={this.handleTermChange} onKeyPress={this.keyPressed}/>
                    <input className={this.state.locationError ? '' : 'input'} placeholder="Where?" onChange={this.handleLocationChange} onKeyPress={this.keyPressed}/>
                </div>
                <div className="SearchBar-submit">
                    <a onClick={this.handleSearch}>Let's Go</a>
                </div>
                <div className="sweet-loading">
                    <ClipLoader
                        css = {barLoaderCss}
                        color = {"#cca353"}
                        loading={this.state.loading}
                    />
                </div>
            </div>
        )
    }
}
export default SearchBar;